<template>
  <div class="contact-page">
    <div class="page-center">
      <div class="contact-header">
        Contact Us
      </div>
      <div class="form-pane">
        <div class="send-text">
          Send us a message
        </div>
        <div class="form" id="contact-form">
          <label class="form-box-label" :style="first.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> First name </label>
          <input type="text" placeholder="First name" class="form-box" v-model="first" autocomplete="new-password"/>
          <label class="form-box-label" :style="last.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Last name </label>
          <input type="text" placeholder="Last name" class="form-box" v-model="last" autocomplete="new-password"/>
          <label class="form-box-label" :style="email.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Email </label>
          <input type="text" placeholder="Email" class="form-box" v-model="email" autocomplete="new-password"/>
          <label class="form-box-label" :style="message.length ? 'opacity: 1; transform: translateY(0px); z-index: 2;' : ''"> Message </label>
          <textarea placeholder="Message" rows=1 class="form-box" v-model="message" autocomplete="new-password"/>
          <button @click="sendMail" class="send-button" :style="email.length && first.length && last.length && message.length ? 'opacity: 1' : 'opacity: 0'"> Send </button>
        </div>
        <div id="form-complete">
          Message sent
        </div>
      </div>
      <div class="form-pane-work">
        <div class="send-text">
          Work with us
          <div class="info-text">
            We are always looking for new talent. Send us a message for more info
          </div>
        </div>
        <div class="send-text space-top">
          Press
          <div class="info-text">
            Get in touch with our PR team by emailing press@bazzled.com for info, interviews, and insight
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return{
      first: '',
      last: '',
      email: '',
      message: '',
    }
  },
  methods:{
    sendMail: async function(){
      const options = {
        method: 'post',
        body: JSON.stringify({first:this.first, last: this.last, email: this.email, message: this.message, messageType: 'Contact Form'}),
        headers: {'Content-Type': 'application/json'}
      };
      fetch('/api/mail', options);
      document.getElementById('contact-form').style.display = 'none';
      document.getElementById('form-complete').style.display = 'block';
      setTimeout(() => document.getElementById('form-complete').style.opacity = 1, 500);
    }
  }
}
</script>
<style scoped>
.contact-page{
  margin: 0 24px;
}
.page-center{
  max-width: 1120px;
  margin: 0 auto;
}
.contact-header{
  padding: 160px 0 100px;
  border-bottom: 1px solid var(--lightest-gray);
  font-size: 40px;
  text-align: left;
}
.form-pane{
  padding: 60px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.send-text{
  font-size: 24px;
  font-weight: 600;
}
.form{
  display: flex;
  flex-direction: column;
  width: 540px;
  align-items: flex-end;
}
.form-box{
  border: none;
  border-bottom: 2px solid var(--lightest-gray);
  margin-bottom: 32px;
  font-size: 14px;
  font-family: inherit;
  padding: 7px 0;
  width: 100% !important;
}
.form-box:focus{
  outline: none;
  border-bottom: 2px solid black;
}
.form-box-label{
  opacity: 0;
  transition: .3s all;
  transform: translateY(20px);
  text-align: left;
  font-size: 12px;
  z-index: -2;
  color: var(--light-gray);
  width: 100%;
}
.send-button{
  padding: 10px 35px;
  text-decoration: none;
  transition: .3s all;
  border-radius: 30px;
  color: white;
  background-color: var(--primary-color);
  border: none;
  font-size: 16px;
  width: 125px;
  margin-right: 0px;
  margin-top: 40px;
}
.send-button:hover{
  background-color: var(--primary-color-light);
}
#form-complete{
  opacity: 0;
  display: none;
  width: 500px;
  height: 390px;
  font-size: 30px;
  transition: 1s all;
  text-align: center;
}
.form-pane-work{
  text-align: left;
  padding: 60px 0 120px;
}
.info-text{
  margin-top: 24px;
  font-size: 16px;
  color: var(--light-gray);
}
.space-top{
  padding-top: 60px;
}
</style>
